<template>
  <div class="store-test">
    <h1>{{ msg }}</h1>
    <p>A页面传过来的参数{{msg2}}</p>
    <div :style="{height: '100px',background: 'red'}" @click="goBack">点我返回A页面</div>
  </div>
</template>

<script>

export default {
  name: 'B',
  computed: {
  },
  data () {
    return {
      msg: 'Welcome to Your B PAGE',
      msg2: ''
    }
  },
  created () {
    console.log('created')
  },
  activated () {
    console.log('active')
  },
  mounted () {
    this.msg2 = this.$route.params.msg
    console.log('mounted')
  },
  methods: {
    goBack () {
      this.$router.back()
      this.$destroy()
    }
  }
}
</script>

